form {
  margin: 2%;
  width: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

input {
  font-size: 20pt;
  height: 50px;
  width: 100%;
  padding-left: 3%;
  border-radius: 15px;
  border: 3px solid #56ff3b;
  box-shadow: 4px 4px #FF0DA1;
  position: relative;
}

.search-btn, .clear-btn {
  background-color: rgb(220, 220, 220);
  border-radius: 30px;
  font-size: 15pt;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  width: 30px;
  height: 30px;
  position: absolute;
  margin-right: 1%;
}

.search-btn {
  transform: rotate(-45deg);
  font-weight: 900;
}

.clear-btn {
  margin-right: 4%;
  font-size: smaller;
}

.search-form {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.no-result-msg {
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: #fff;
  border-radius: 15px;
  padding: 10px;
  text-shadow: 0px 0px 5px #fff, 0px 0px 10px #56ff3b;
  font-size: 2.5rem;
  animation: laser 3s linear infinite;
}

@keyframes laser {
  0% {
    box-shadow: 0px 0px 10px #56ff3b, 
      0px 0px 20px #56ff3b, 
      0px 0px 30px #56ff3b, 
      0px 0px 40px #56ff3b, 
      0px 0px 50px #56ff3b, 
      0px 0px 75px #56ff3b;
  }
  50% {
    box-shadow: 0px 0px 5px #56ff3b, 
      0px 0px 10px #56ff3b, 
      0px 0px 15px #56ff3b, 
      0px 0px 20px #56ff3b, 
      0px 0px 30px #56ff3b, 
      0px 0px 40px #56ff3b, 
      0px 0px 50px #56ff3b;
  }
  100% {
    box-shadow: 0px 0px 10px #56ff3b, 
      0px 0px 20px #56ff3b, 
      0px 0px 30px #56ff3b, 
      0px 0px 40px #56ff3b, 
      0px 0px 50px #56ff3b, 
      0px 0px 75px #56ff3b;
  }
}